import React, {Component} from 'react';
import './App.css';

class App extends Component{
  constructor(props){
    super(props);

    this.state = {
      images: [{image: "https://cdn.shopify.com/s/files/1/0546/1817/files/1_grande.jpg?v=1510086340",
      caption: "Mid Century sofa"},
    {image: "https://hivemodern.com/public_resources/eames-lounge-chair-ottoman-charles-and-ray-eames-herman-miller-1.jpg",
  caption: "Herman Miller Eames Chair"}],
    };

    this.addImage = this.addImage.bind(this);

  }

  addImage (newImage){
    const images = [...this.state.images];
    images.push(newImage);
    this.setState({images});
    console.log(images);
  }

  render(){
    return (
      <div className="App">
        <ImageBoard addImage={this.addImage} images={this.state.images}/>
      </div>
    );
  }
}




class ImageForm extends Component{

  constructor(props){
    super(props);
    this.state = {
      image: "",
      caption: '',
    }

  this.doSubmit = this.doSubmit.bind(this);
  this.doInput = this.doInput.bind(this);

  }



  doSubmit(event){
    event.preventDefault();
    this.props.addImage(this.state);
    this.setState({image: ""});
    this.setState({caption: ""});
  }

  doInput(event){
    // console.log(event.target.value);
    this.setState({[event.target.name]: event.target.value});
  }



  render() {
    return(
      <form onSubmit={this.doSubmit}>
      <input placeholder="Enter Image URL" name="image" type="text" id="img-text" value={this.state.image} onChange={this.doInput} required/><br/>
      <input placeholder="Enter Image Caption" name="caption" type="text" id="cap-text" value={this.state.caption} onChange={this.doInput} required/><br/>
      <button id="submitButton" type="submit">Submit Image</button>
      </form>
)
}

  }






class ImageList extends Component{

  render() {

    const images = this.props.images.map((image, index) => (
      <li key={index} className="imageItem" id="imgBox">
      <img className="imageText" src={image.image} alt="bigthing"/>
      <p> {image.caption}</p>
      </li>
    ))
    return(
      <ul className="imageList">
        {images}
      </ul>
    )
  }
};







class ImageBoard extends Component{
  render() {
    return(
      <div id="displayWindow">
        <ImageForm addImage={this.props.addImage}/>
        <ImageList images={this.props.images}/>
      </div>
    )
  }
}















export default App;
